li,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  font-size: normal;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Bold.woff") format("woff");
  font-weight: 700;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Light.woff") format("woff");
  font-weight: 300;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Regular.woff") format("woff");
  font-weight: 400;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Medium.woff") format("woff");
  font-weight: 500;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Italic.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Italic.woff") format("woff");
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: 'Bernier Distressed';
  src: url("../fonts/Bernier/bernierdistressed-regular-webfont.woff2") format("woff2"), url("../fonts/Bernier/bernierdistressed-regular-webfont.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Bernier';
  src: url("../fonts/Bernier/fonts/bernierregular-regular-webfont.woff2") format("woff2"), url("../fonts/Bernier/bernierregular-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Bernier Shade';
  src: url("../fonts/Bernier/berniershade-regular-webfont.woff2") format("woff2"), url("../fonts/Bernier/berniershade-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

body {
  font-family: 'Roboto';
  font-size: 16px;
}

.hero {
  background-image: url("../images/bg.jpg");
  background-size: cover;
  min-height: 620px;
  height: 100%;
  position: relative;
}

.hero__intro {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero__arrow {
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -100%);
  z-index: 10;
}

.hero__triangles {
  position: absolute;
  top: 100%;
  left: 0;
  transform: translateY(-100%);
}

.header {
  display: flex;
}

.header__socials {
  margin-right: auto;
  margin-top: 3%;
  margin-left: 3%;
}

.menu {
  display: block;
  width: 36px;
  height: 36px;
  position: relative;
  margin-top: 3%;
  margin-right: 3%;
}

.menu__icon {
  height: 5px;
  width: 100%;
  background-color: #fff;
  border-radius: 2px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.menu__icon:before {
  content: '';
  display: block;
  width: 36px;
  height: 5px;
  position: absolute;
  bottom: 300%;
  background-color: #fff;
  border-radius: 2px;
}

.menu__icon:after {
  content: '';
  display: block;
  width: 36px;
  height: 5px;
  position: absolute;
  top: 300%;
  background-color: #fff;
  border-radius: 2px;
}

.arrow {
  height: 20px;
  width: 25px;
}

.arrow__icon {
  fill: rgba(255, 255, 255, 0.7);
}

.arrow__icon:hover {
  transition: .3s;
  fill: #fff;
}

.triangles__left {
  fill: #efeee9;
}

.triangles__right {
  fill: #eeebdf;
}

.intro {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.intro__avatar {
  border-radius: 50%;
  max-width: 40%;
  max-width: 150px;
}

.intro__text {
  display: flex;
  flex-direction: column;
}

.intro__name {
  font-size: 28px;
}

.intro__name_welcome {
  color: #455a64;
}

.intro__name_hero {
  color: white;
}

.intro__site {
  font-weight: 300;
}

.intro__site_welcome {
  color: rgba(69, 90, 100, 0.85);
}

.intro__site_hero {
  color: rgba(255, 255, 255, 0.85);
}

.social {
  display: block;
  height: 100%;
  width: 100%;
}

.social__icon_welcome {
  fill: #0ab19e;
}

.social__icon_welcome:hover {
  transition: .3s;
  fill: rgba(0, 150, 136, 0.7);
}

.social__icon_hero,
.social__icon_footer {
  fill: rgba(255, 255, 255, 0.7);
}

.social__icon_hero:hover,
.social__icon_footer:hover {
  transition: .3s;
  fill: #fff;
}

.social__icon_footer {
  width: 36px;
  height: 36px;
}

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
}

.socials__item:not(:last-of-type) {
  margin-right: 30px;
}

.socials__item_footer:not(:last-of-type) {
  margin-right: 10px;
}

.subtitle {
  display: flex;
  justify-content: center;
}

.subtitle__text {
  text-transform: uppercase;
  font-family: 'Bernier';
  width: fit-content;
  color: #455a64;
  position: relative;
  font-size: 35px;
}

.subtitle__text:before {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-200%, -50%);
}

.subtitle__text:after {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(100%, -50%);
}

.subtitle__text:hover:after,
.subtitle__text:hover:before {
  width: 25px;
  height: 5px;
  transition: width .5s, height .5s;
}

.title {
  display: flex;
  justify-content: center;
}

.title__text {
  text-transform: uppercase;
  font-family: 'Bernier';
  width: fit-content;
  color: #455a64;
  font-size: 63px;
  position: relative;
}

.title__text:before {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-200%, -50%);
}

.title__text:after {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(100%, -50%);
}

.title__text:hover:after,
.title__text:hover:before {
  width: 25px;
  height: 5px;
  transition: width .5s, height .5s;
}

.personal {
  min-width: 620px;
  display: flex;
}

.personal__left-col {
  width: 50%;
  padding: 0 8% 4% 8%;
  background-color: #efeee9;
}

.personal__right-col {
  width: 50%;
  padding: 0 2% 4% 2%;
  background-color: #eeebdf;
}

.avatar-area__title {
  margin-bottom: 70px;
}

.avatar-area__img {
  max-width: 100%;
}

.text-area {
  color: #455a64;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.text-area h1 {
  width: fit-content;
  max-width: 60%;
  text-transform: uppercase;
  font-family: 'Bernier';
  font-size: 35px;
  text-align: center;
  margin-bottom: 40px;
  margin-top: 40px;
  position: relative;
}

.text-area h1:before {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-200%, -50%);
}

.text-area h1:after {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(100%, -50%);
}

.text-area h1:hover:after,
.text-area h1:hover:before {
  width: 25px;
  height: 5px;
  transition: width .5s, height .5s;
}

.text-area_left h1:first-of-type {
  margin-top: 0;
}

.skills__title {
  font-size: 28px;
  font-weight: 400;
  color: #455a64;
}

.skills__title::first-letter {
  text-transform: uppercase;
}

.skills__list {
  display: flex;
}

.skills__item:hover .circle__sector {
  stroke-dashoffset: 125.6;
  transition: stroke-dashoffset 1s;
}

.skills__item:not(:last-of-type) {
  margin-bottom: 35px;
}

.circle {
  position: relative;
  width: 120px;
  height: 120px;
}

.circle__icon {
  fill: none;
  stroke-width: 20;
}

.circle__main {
  stroke: #ccc;
  fill: none;
  position: relative;
}

.circle__sector {
  stroke: #0ab19e;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
  stroke-dasharray: 251.2;
  stroke-dashoffset: 251.2;
  transition: stroke-dashoffset 1s;
}

.circle__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #0ab19e;
  max-width: 70px;
  word-wrap: break-word;
  text-align: center;
  font-size: 12px;
  line-height: 14.7px;
}

.map {
  background-image: url("../images/bg.jpg");
  background-size: cover;
  height: 100%;
  position: relative;
}

.map__contacts {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.map__footer {
  position: absolute;
  top: 100%;
  width: 100%;
  transform: translateY(-100%);
}

.contacts {
  background-color: rgba(255, 255, 255, 0.7);
  padding: 40px;
  color: #455a64;
}

.contacts__subtitle {
  padding-bottom: 40px;
}

.contacts__icon {
  fill: #0ab19e;
}

.contacts__item {
  display: flex;
}

.contacts__item:not(:last-of-type) {
  margin-bottom: 25px;
}

.contacts__icon {
  margin-right: 10px;
}

.footer {
  background-color: rgba(55, 62, 66, 0.6);
  color: rgba(255, 255, 255, 0.7);
}

.footer__row {
  display: flex;
  justify-content: space-between;
  padding: 15px 30px;
}

.footer__row:not(:last-of-type) {
  border-bottom: 1px solid #6b7879;
}

.footer__col {
  width: 40%;
  display: flex;
}

.footer__col_right {
  justify-content: flex-end;
}

.footer__menu-text,
.footer__credits {
  height: fit-content;
  margin: auto 0;
}

.menu-text {
  display: flex;
  color: #fff;
}

.menu-text__item:not(:last-of-type) {
  margin-right: 40px;
}

.main {
  background-image: url("../images/bg.jpg");
  background-size: cover;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.main__auth {
  align-self: flex-end;
  margin: 20px;
}

.main__welcome {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main__credits {
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 3%;
}

.welcome {
  text-align: center;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 4px;
}

.welcome__socials {
  margin-top: 25px;
}

.welcome__info {
  display: flex;
  flex-direction: column;
  padding: 35px 60px;
}

.welcome__avatar {
  border-radius: 50%;
  max-width: 40%;
  max-width: 150px;
}

.welcome__text {
  display: flex;
  flex-direction: column;
  color: #455a64;
}

.welcome__name {
  font-size: 28px;
}

.buttons {
  display: flex;
  border-radius: 0 0 4px 4px;
  font-size: 16px;
  color: #fff;
}

.buttons__item {
  flex: auto;
}

.buttons__item:not(:last-of-type) {
  border-right: 2px solid #73cac2;
}

.button {
  display: block;
  width: 100%;
  height: 100%;
  padding: 15px 25px;
  background-color: #0ab19e;
}

.button_works {
  border-radius: 0 0 0 4px;
}

.button_blog {
  border-radius: 0 0 4px 0;
}

.button:hover {
  transition: .3s;
  background-color: rgba(0, 150, 136, 0.7);
}

.btn-auth {
  display: block;
  height: 100%;
  padding: 15px 25px;
  border-radius: 4px;
  border: 1px solid white;
  color: white;
}

.btn-auth:hover {
  border: 1px solid transparent;
  transition: background-color .3s, color .3s;
  background-color: white;
  color: #0ab19e;
  font-weight: 500;
}